.search-from {
  &__section {
    background-color: #00000000;

    span {
      height: 1px;
      background-color: #707070;
      width: 100%;
      padding-right: 20px;
    }
    h2 {
      font-size: 24px;
      font-weight: bold;
      text-transform: uppercase;
      padding: 0;
      margin: 0;
      max-width: fit-content;
      width: 100%;
      position: relative;
      padding-right: 15px;
    }
    .container {
      background-image: url("../../assets/images/search-Bg.png");
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      height: 177px;
      padding: 0 50px;
      border-radius: 40px;
    }
    form {
      input,
      select {
        height: 47px;
        padding: 0 15px;
        background-color: #bb9875 !important;
        border-radius: 50px;
        color: #fff;
        border: none;
        &::placeholder {
          color: #fff;
        }
        &:focus {
          outline: none;
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  .search-from {
    &__section {
      .container {
        padding: 0 10px;
      }
    }
  }
}
@media (max-width: 600px) {
  .search-from {
    &__section {
      h2 {
        font-size: 20px;
      }
      .container {
        height: 250px;
        padding: 0 10px;
        border-radius: 0px;
      }
      form {
        input,
        select {
          &::placeholder {
            color: #fff;
          }
          &:focus {
            outline: none;
          }
        }
      }
    }
  }
}
