.header {
  &__section {
    position: relative;
    &--height {
      height: 90px;
      background-color: #f1f1f1;
    }
    &--color {
      background-color: #363b3f;
      height: 50px;
    }
  }
  &__logo {
    img {
      position: absolute;
      top: 0;
      z-index: 99;
    }
  }
  &__actions {
    color: #fff;
    h4 {
      font-size: 14px;
      color: #bb9875;
      font-weight: 400;
      width: max-content;
    }
    a {
      color: #fff;
      font-size: 14px;
      font-weight: 400;
      width: max-content;
      text-decoration: none;
      &:hover {
        color: #bb9875;
      }
    }
  }
  &__navlinks {
    ul {
      li {
        list-style: none;
        cursor: pointer;
        .dropdown-menu-dark .dropdown-item:active{
 
            background-color: #bb9875;
          
        }
        a {
          color: #080808;
          &:hover {
            color: #bb9875;
          }
          &:focus {
            color: #bb9875 !important;
          }
          &:active {
            color: #bb9875 !important;
          }
        }
        &.active {
          color: #bb9875;
        }
      }
      li:hover {
        color: #bb9875;
      }
    }
  }
  &__socailicons {
    &--icons {
      padding: 7px 10px;
      border: 1px solid #2e303f;
      border-radius: 50px;
      cursor: pointer;
      &:hover {
        background-color: #2e303f;
        svg {
          color: #fff;
        }
      }
    }
  }
}
@media (max-width: 1023px) {
  .header {
    height: 50px;
    .btn-menu-toggle {
      background-color: transparent;
      border-radius: 0;
      border: none;
      svg {
        font-size: 25px;
        color: #ffffff;
        transition: all 0.3s ease-in-out;
      }
      &:hover {
        svg {
          color: #ffffff;
        }
      }
    }
    &__navlinks {
      &__wrapper {
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        visibility: hidden;
        z-index: 997;
        transition: all 0.2s ease-in-out;
        &.opened {
          opacity: 1;
          visibility: visible;
          nav {
            transform: translateX(0);
          }
        }
        nav {
          width: 300px;
          max-width: 80%;
          position: fixed;
          height: 100vh;
          top: 0;
          right: 0;
          z-index: 998;
          background-color: #bb9875;
          flex-direction: column;
          transform: translateX(320px);
          transition: all 0.6s ease-in-out;
          .toggle-menu-btn {
            background-color: transparent;
            border-radius: 0;
            border: none;
            padding: 20px;
            svg {
              font-size: 25px;
              color: #fafafa;
              transition: all 0.3s ease-in-out;
              path {
                stroke: #ffffff !important;
              }
            }
            &:hover {
              svg {
                color: #000000;
              }
            }
          }
          ul {
            padding: 30px 20px 0 !important;
            width: 100% !important;
            display: block !important;
            li {
              display: block;
              color: #fff;
              margin: 20px 0;
              border-bottom: 1px solid #363b3f4a;
              padding-bottom: 5px;
              padding-left: 5px;

              a {
                display: block;
                border-radius: 0 !important;
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 600px) {
  .header {
    &__section {
      &--color {
        .header__logo img {
          width: 40%;
        }
      }
    }
  }
}
