.get-in-touch {
  background-color: #2b2f3b;
  position: relative;
  .bg-img {
    background-image: url("../../assets/images/get-in-touch2.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-radius: 0 60px 0 0;
    margin: 0 -70px 0 0;
    padding-left: 7%;
  }
  .col-img {
    img {
      width: 115% !important;
      transform: translateY(25px);
    }
  }

  &__title {
    background-image: url("../../assets/images/Icon.png");
    background-position: left center;
    background-repeat: no-repeat;
    background-size: contain;
    padding-top: 50px;
    padding-left: 50px;
    text-align: left;
    h3 {
      font-size: 35px;
      font-weight: 500;
      text-transform: uppercase;
      color: #ffffff;
      margin-bottom: 0;
    }
    h2 {
      font-size: 55px;
      font-weight: bold;
      color: #fff;
    }
  }
  .min-height{
    min-height:423px;
  }
  form {
    padding-bottom: 30px;
    input,
    textarea {
      background-color: #fff;
      border-radius: 25px;
      padding-left: 15px;
      border: none;
      margin: 10px 0;
      color: #000;
      &::placeholder {
        color: #000;
      }
      &:focus {
        outline: none;
      }
    }
    textarea {
      padding-top: 10px;
    }
    input {
      height: 47px;
    }
  }
}

@media (max-width: 600px) {
  .get-in-touch {
    .bg-img {
      padding: 15px !important;
    }
    .col-img {
      display: none !important;
      img {
        max-width: 100% !important;
      transform: translateY(0px);

      }
    }
  }
}

@media (max-width: 768px) {
  .get-in-touch {
    .container{
      padding: 0 !important;
    }
    .col-img {
      display: none !important;
      img {
        max-width: 100% !important;
      transform: translateY(0px);

      }
    }
  }
}
