.book-card__image {
  color: #fff;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 369px;
  h2 {
    font-size: 22px;
    font-weight: 500;
  }
  h1 {
    font-size: 32px;
    font-weight: bold;
  }
}
@media screen and (min-width: 1700px) { 
  .book-card__image {
    background-size: 95% 100%;
    height: 450px;
  }
}
