.story-forum {
  background-color: #2b2f3b;
  padding: 100px 0 190px;
  margin-bottom: -40px;
  img {
    z-index: 2;
    width: 100%;
  }
  &__content {
    border: 5px solid #bb9875;
    border-radius: 60px;
    background: linear-gradient(
      140deg,
      rgba(187, 152, 117, 0) 19%,
      rgba(187, 152, 117, 0.4) 95%,
      rgba(187, 152, 117, 0.4) 100%
    );
    height: 100%;
    padding-left: 100px;
    margin-left: -90px;
    margin-top: 45px;

    &__title {
      background-image: url("../../assets/images/titleBackground.png");
      background-position: left -40px;
      background-repeat: no-repeat;
      background-size: auto;
      padding-top: 50px;
      text-align: left;
      padding-left: 50px;
      // h3 {
      //   font-size: 35px;
      //   font-weight: 500;
      //   text-transform: uppercase;
      //   color: #fff;
      //   margin-bottom: -6px;
      // }
      // h2 {
      //   font-size: 55px;
      //   font-weight: bold;
      //   letter-spacing: 3px;
      //   text-transform: uppercase;
      //   color: #bb9875;
      // }
    }
  }
}

@media (max-width: 1024px) {
  .story-forum {
    img {
      width: 100%;
    }
    &__content {
      padding-left: 10px;
      margin-left: 0px;
      margin-top: 45px;
    }
  }
}

@media (max-width: 600px) {
  .story-forum {
    img {
      width: 100%;
    }
    &__content {
      padding-left: 10px;
      margin-left: 0px;
      margin-top: 45px;
    }
  }
}
