.feature-stories {
  background-color: #2b2f3b;
  background-image: url("../../assets/images/Shape.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 -480px;

  &__title {
    background-image: url("../../assets/images/titleBackground.png");
    background-position: left -40px;
    background-repeat: no-repeat;
    background-size: auto;
    padding-top: 50px;
    text-align: left;
    padding-left: 50px;
    h3 {
      margin-bottom: -6px;
    }
  }
  p {
    max-width: 780px;
  }
  .top-stories {
    background-image: url("../../assets/images/topStory.png");
    height: 100%;
    padding-top: 40px;
    border: 3px solid #bb9875;
    border-radius: 60px 0 0 0;
    &__cards {
      &__wrapper {
        height: 1000px;
        margin-right: 30px;
        overflow-y: scroll;
      }
    }
  }
}
@media (max-width: 600px) {
  .feature-stories {
    &__title {
    background-position: left;
    background-size: contain;

    }
    .top-stories {
      &__cards {
        &__wrapper {
          margin-right: 12px;
        }
      }
    }
  }
}
::-webkit-scrollbar {
  margin-right: 10px;
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 10px;
  margin-right: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #bb9875;
  border-radius: 50px;
  max-height: 20px;
}
.mostly-customized-scrollbar::-webkit-scrollbar {
  width: 5px;
  margin-right: 10px;
  height: 8px;
  background-color: rgb(0, 0, 0); /* or add it to the track */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #684f36;
}
