.home-banner {
  min-height: 500px;
  height: 100vh;
  position: relative;
  background-color: #363b3f;
  overflow: hidden;
  .banner-slide {
    min-height: 500px;
    height: 100vh;
    background-size: cover;
    background-position: center;
    .banner-slide__content {
      padding: 245px 0;
      .slide-content {
        z-index: 1;
        max-width: 100%;
        width: 100%;
        text-align: center;
        color: #fff;
        span {
          border: 1px solid #fff;
          height: 57px;
          width: 170px;
          transform: translate(0px, 25px);
          position: relative;
          &:before {
            content: "";
            height: 1px;
            background: #fff;
            width: 20px;
            position: absolute;
            bottom: 0;
          }
          &.first {
            border-width: 1px 0 0px 1px;
            &:before {
              left: 0;
            }
          }
          &.last {
            border-width: 1px 1px 0px 0px;
            &:before {
              right: 0;
            }
          }
        }
        h4 {
          font-size: 45px;
          font-weight: 500;
        }
        h2 {
          color: #bb9875;
          font-size: 80px;
          font-weight: bold;
          margin-top: -18px !important;
        }
        p {
          max-width: 895px;
          margin: 40px 0;
        }
      }
    }
  }
  .slick-dots {
    position: absolute;
    display: flex !important;
    top: 0;
    right: 0;
    z-index: 99;
    width: 25px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    li {
      width: auto;
      height: auto;
      margin: 0;
      button {
        height: 20px !important;
        font-size: 30px;
        font-weight: bold;
        width: 10px !important;
        color: #fff;
        margin: 20px 25px 20px 0;
        transition: all 0.3s ease-in-out;
        position: relative;
        &:before {
          content: "";
          color: #fff;
          background: #fff;
          width: 2px;
          height: 70%;
          position: absolute;
          left: 14px;
          top: -90%;
          opacity: 1;
        }
      }
      &.slick-active {
        button {
          height: 50px !important;
        }
        button:before {
          height: 30px;
          border-radius: 10px;
          top: -20px;
        }
      }
    }
  }
}

@media only screen and (max-width: 1400px) {
  .banner-slider {
    .banner-slide {
      .banner-slide__content {
        .slide-content {
          z-index: 1;
          max-width: 800px;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .home-banner {
    .slick-dots {
      li {
        button{
          font-size: 18px;
        }
        button:before {
          left: 10px;
        }
      }
    }
    .banner-slide {
      .banner-slide__content {
        padding: 45px 0;
        .slide-content {
          span {
            width: 80px;
            height: 38px;
          }
          h4 {
            font-size: 20px;
            transform: translateY(10px);
          }
          h2 {
            margin-top: 0px !important;
            font-size: 35px;
          }
        }
        p {
          max-width: 285px !important;
          margin: 40px 0;
        }
      }
    }
  }
}
