.top-story-card {
    padding: 0 15px 0 30px;
  &__wrapper {
      border-bottom: 1px solid #D8D8D8;
      padding: 30px 0;
      img{
        width: 100%;
      }
  }
  &__content {
      padding-left: 15px;
    h2 {
      font-size: 16px;
      font-weight: bold;
      color: #ffffff;
    }
    &__metadata {
      h4 {
        font-size: 18px;
        font-weight: 400;
        color: #ffffff;
        span {
          color: #bb9875;
          font-weight: bold;
        }
      }
    }
  }
}

