.article-card {
  text-align: center;
  outline: 1px solid #ebe8e8;
  border-radius: 50px;
  outline-offset: -3px;
  margin-bottom: 18px !important;
  img {
    filter: brightness(0.7);
    transform: translateY(-4px);
    width: 109%;
  }
  &__metadata {
    transition: 0.3s ease-in-out;
    border: 2px solid #ffffff;
    max-width: 323px;
    width: 100%;
    border-radius: 100px;
    min-height: 40px;
    transform: translateY(-34px);
    h4 {
      font-size: 18px;
      font-weight: 400;
      color: #ffffff;
      span {
        color: #bb9875;
        font-weight: bold;
      }
    }
  }
  h2 {
    font-size: 19px;
    font-weight: bold;
    color: #fff;
  }
  button {
    transform: translateY(16px);
  }
}

.article-card:hover {
  .article-card {
    &__metadata {
      background-color: #ebe8e8;
      h4 {
        color: #000000;
      }
    }
  }
  button {
    background-color: #ffffff;
    color: #000000;
    svg {
      background-color: #bb9875;
      fill: #fff;
    }
  }
}

@media (max-width: 1024px) {
  .article-card {
    img {
      width: 114%;
      // transform: translateY(10px);
    }
    &__metadata {
      transform: translateY(-25px);
    }
  }
}

@media (max-width: 600px) {
  .article-card {
    img {
      width: 100%;
      transform: translateY(10px);
    }
    &__metadata {
      transform: translateY(-15px);
    }
  }
}
