.article-blog {
  background-color: #2b2f3b;
  background-image: url("../../assets/images/Shape2.png");
  background-position: left 40px;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 100px 0 0px;
  &__title {
    background-image: url("../../assets/images/titleBackground.png");
    background-position: center -40px;
    background-repeat: no-repeat;
    background-size: auto;
    padding-top: 50px;
    text-align: center;
    // padding-left: 50px;
    // h3 {
    //   font-size: 35px;
    //   font-weight: 500;
    //   text-transform: uppercase;
    //   color: #fff;
    //   margin-bottom: -6px;
    // }
    // h2 {
    //   font-size: 55px;
    //   font-weight: bold;
    //   letter-spacing: 3px;
    //   text-transform: uppercase;
    //   color: #bb9875;
    // }
  }
  p {
    letter-spacing: 0;
    max-width: 1192px;
    text-align: center;
  }
  .slick-prev:before,
  .slick-next:before {
    font-size: 30px;
    opacity: 1;
  }
  .slick-prev,
  .slick-next {
    color: #bb9875;
    border: 1px solid #bb9875;
    border-radius: 50px;

    padding: 3px;
  }
  .slick-dots{
    display: none !important;
  }
  .slick-prev:hover,
  .slick-next:hover {
    color: #000000 !important;
    border-color: #ffffff;
    background-color: #ffffff;
  }
  // .slick-prev:before{
  //   content: "/f060";
  //   font-family: 'font awesome Free';
  // }
}

@media (max-width: 600px) {
  .article-blog {
    &__title {
      background-position: center;
      background-size: contain;
      padding-left: 0px;
    }
    .slick-next {
      right: 0;
    }
    .slick-prev {
      left: -12px;
    }
  }
}
