.story-card {
  transition: 0.3s all;
  &__wrapper {
    img {
      width: 100%;
      cursor: pointer;
    }
  }
  &__content {
    color: #c9c9c9;
    h2 {
      font-size: 20px;
      cursor: pointer;
      font-weight: bold;
      color: #ffffff;
      transition: 0.3s all;
    }
    &__metadata {
      h4 {
        font-size: 18px;
        font-weight: 400;
        color: #c9c9c9;
        span {
          color: #bb9875;
          font-weight: bold;
        }
      }
      svg {
        color: #c9c9c9;
      }
    }
    &__metadata {
      h4 {
        font-size: 18px;
        font-weight: 400;
        color: #ffffff;
        span {
          color: #bb9875;
          font-weight: bold;
        }
      }
    }
  }
  &:hover {
    h2 {
      color: #bb9875;
    }
  }
}

@media (max-width: 600px) {
  .story-card {
    &__wrapper {
      img {
        width: 100%;
      }
    }
  }
}

@media (max-width: 1024px) {
  .story-card {
    &__wrapper {
      img {
        width: 100%;
      }
    }
  }
}
